import { default as allExercises } from '../assets/exercises';
import _ from 'lodash';

export default (state = {}, action) => {
  const { mode } = action;
  switch (action.type) {
    case 'GET_EXERCISES': {
      const { [mode]: exercises = [] } = allExercises;
      const { [mode]: { latestPassword } = {}} = state;
      const latestIndex = exercises.findIndex(e => e.password === latestPassword);
      if (latestIndex === -1) {
        return [];
      }
      return exercises.slice(0, latestIndex);
    }
    case 'UNLOCK_EXERCISE': {
      const {password: newPassword} = action;
      const { [mode]: exercises = [] } = allExercises;
      const { [mode]: currentState = {} } = state;
      const { currentPassword } = currentState;

      if (!newPassword) {
        return state;
      }

      const currentExerciseIndex = !currentPassword ? -1 : exercises.findIndex(({password}) => password.toUpperCase() === currentPassword.toUpperCase());
      const newExerciseIndex = _.findIndex(exercises, ({password}) => password.toUpperCase() === newPassword.toUpperCase());

      if (newExerciseIndex !== -1) {
        if (_.isEmpty(currentState) || newExerciseIndex >= currentExerciseIndex) {
          return {
            ...state,
            [mode]: {
              latestPassword: newPassword.toUpperCase()
            }
          }
        }
      }
      return state;
    }  
   default:
    return state
  }
 }