import React from 'react';

import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    minWidth: 275,
    margin: 20
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
};

class Landing extends React.Component {
  navigate(mode = '') {
    window.location.href = `${window.location.href}?mode=${mode}`;
  }
  render() {
    const { classes } = this.props;
    return (
      <Card className={classes.card}>
        <CardContent>
          <Typography variant="h5" component="h2">
            Please Choose a Category
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small" onClick={() => this.navigate('ILIAD')}>Iliad</Button>
          <Button size="small" onClick={() => this.navigate('AENEID')}>Aeneid</Button>
          <Button size="small" onClick={() => this.navigate('ODYSSEY')}>Odyssey</Button>
          <Button size="small" onClick={() => this.navigate('CLEAR')}>Clear</Button>

        </CardActions>
      </Card>
    )
  }
}

export default withStyles(styles)(Landing);