import React from 'react';
import { connect } from 'react-redux';

import { Button, Paper, TextField, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import hermes from '../assets/hermes.png';
import { unlockExercise } from '../actions/exerciseActions';

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  passwordContainer: {
    flex: 2
  },
  imageContainer: {
    flexGrow: 5
  },
  imageFigure: {
    backgroundImage: `url(${hermes})`, 
    backgroundRepeat: 'no-repeat', 
    backgroundPosition: 'center', 
    backgroundSize: '100%',
    height: 0,
    paddingTop: '100%',
    width: '100%',
    position: 'relative'
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  dense: {
    marginTop: 16,
  },
  menu: {
    width: 200,
  },
  button: {
    margin: theme.spacing.unit,
  },
  vBox: {
    display: 'flex',
    flexDirection: 'column',
    flex: 1,
    flexShrink: 1,
  },
  hBox: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1,
    flexShrink: 1
  }
});

class CenteredElement extends React.Component {
  render() {
    const spacer = <div style={{flex: 1}} />;
    return (
      <div className={this.props.class}>
        {spacer}
        {this.props.children}
        {spacer}
      </div>
    )
  }
}

class PasswordPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password: ''
    };
  }
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };
  submitPassword() {
    this.props.unlockExercise(this.props.mode, this.state.password);
    this.setState({
      password: ''
    });
  }

  render() {
    const { classes } = this.props;
    const containerStyle = {
      backgroundImage: `url(${hermes})`, 
      backgroundRepeat: 'no-repeat', 
      backgroundPosition: 'center', 
      backgroundSize: '100%',
      height: 0,
      paddingTop: 'calc(100% / 1.6)', // Aspect ratio is 16:10
      position: 'relative'
    };
    const whiteboardStyle = {
      position: 'absolute', 
      top: '25%', 
      left: '0%', 
      width: '45%', 
      height: '75%', 
      overflow: 'auto', 
      fontSize: '1.25vw'
    };
    return (
      <div style={containerStyle}>
        <div style={whiteboardStyle}>
        <Paper style={{minWidth: 200}}>
            <CenteredElement class={classes.hBox}>
              <Typography variant="h6">
                Please sign here
              </Typography>
            </CenteredElement>
            <CenteredElement class={classes.hBox}>
              <TextField
              id="password"
              label="Password"
              type="password"
              className={classes.textField}
              value={this.state.password}
              onChange={this.handleChange('password')}
              margin="normal"
              variant="outlined"
              />
            </CenteredElement>
            
            <CenteredElement class={classes.hBox}>
              <Button color="primary" className={classes.button} onClick={() => this.submitPassword()} value="submit">
                Login
              </Button>
            </CenteredElement>
           
          </Paper>
        </div>
      </div>
   );
  }
}


const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
  unlockExercise: (mode, password) => dispatch(unlockExercise(mode, password))
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, mapDispatchToProps)(PasswordPage));