import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import rootReducer from './reducers/rootReducer';


const sampleState = JSON.stringify({
  exercises: {}
});

export default function configureStore() {
  try {
    const persistedState = JSON.parse(localStorage.getItem('classics-is-cool') || sampleState);
    const store = createStore(
      rootReducer,
      persistedState,
      applyMiddleware(thunk)
    );
  
    store.subscribe(() => {
      localStorage.setItem('classics-is-cool', JSON.stringify(store.getState()));
    });
  
    return store;
  } catch (err) {
    clear();
    return configureStore();
  }
}

export const clear = () =>  {
  return localStorage.setItem('classics-is-cool', sampleState);
}

