import React from 'react';
import teacher from '../assets/teacher.png';

import { MISSION as ILIAD } from '../assets/exercises/iliad';
import { MISSION as AENEID } from '../assets/exercises/aeneid';
import { MISSION as ODYSSEY } from '../assets/exercises/odyssey';

const CONTENT = {
  ILIAD,
  AENEID,
  ODYSSEY
}
const containerStyle = {
  backgroundImage: `url(${teacher})`, 
  backgroundRepeat: 'no-repeat', 
  backgroundPosition: 'center', 
  backgroundSize: '100%',
  height: 0,
  paddingTop: 'calc(100% / 1.6)', // Aspect ratio is 16:10
  position: 'relative'
};
const whiteboardStyle = {
  position: 'absolute', 
  top: '18%', 
  left: '5%', 
  width: '28%', 
  height: '37%', 
  overflow: 'auto', 
  fontSize: '1.25vw'
};




function getContentForMode(mode = '') {
  return CONTENT[mode.toUpperCase()];
}

class HomePage extends React.Component {
  render() {
    const { params } = this.props;
    const { mode } = params;
    return (
      <div style={containerStyle}>
        <div style={whiteboardStyle}>
          {getContentForMode(mode)}
        </div>
      </div>
   );
  }
}

export default HomePage;