import _ from 'lodash';
import ILIAD from './iliad';
import AENEID from './aeneid';
import ODYSSEY from './odyssey';

const _exerciseMap = {
  ILIAD,
  AENEID,
  ODYSSEY
}

const exerciseMap = _.mapValues(_exerciseMap, val => val.filter(exercise => exercise.type !== 'EXTRA'));

export default exerciseMap;

export const getExercises = (mode, latestPassword = '') => {
  const { [mode]: exercises = [] } = exerciseMap;
  const latestIndex = exercises.findIndex(e => e.password.toUpperCase() === latestPassword.toUpperCase());
  if (latestIndex === -1) {
    return [];
  }
  return exercises.slice(0, latestIndex + 1);
}

const extraExerciseMap = _.mapValues(_exerciseMap, val => val.filter(exercise => exercise.type === 'EXTRA'));
export const getExtraExercises = mode => {
  const { [mode]: exercises = [] } = extraExerciseMap;
  return exercises;
}