import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import { AppBar, Toolbar, Typography, IconButton, CssBaseline } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';

import HomePage from './pages/Home';
import LandingPage from './pages/Landing';
import Drawer, { drawerWidth } from './components/Drawer';
import { clear } from './store';
import queryString from 'query-string'

import _ from 'lodash';

const styles = theme => ({
  root: {
    display: 'flex',
    height: '100%'
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    marginLeft: drawerWidth,
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
    },
  },
  menuButton: {
    marginRight: 20,
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
  grow: {
    flexGrow: 1
  }
});


class App extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
  };
  constructor(props) {
    super(props);
    const params = queryString.parse(this.props.location.search);
    this.state = {
      drawerOpen: false,
      params
    }
  }
  componentDidMount() {
    this.viewHome();
  }
  toggleDrawer(open) {
    this.setState({
      drawerOpen: open
    });
  }

  viewHome() {
    this.navigate({
      title: 'Home',
      content: <HomePage params={this.state.params} />,
      viewingHome: true
    });
  }

  navigate({title, content, viewingHome = false}) {
    this.setState({
      title,
      content,
      viewingHome
    });
  }

  render() {
    const { title, content, viewingHome, drawerOpen, params } = this.state;
    const { classes, isAdmin} = this.props;

    if (_.isEmpty(params)) {
      return (
        <LandingPage />
      );
    }

    if (params.mode === 'CLEAR') {
      clear();
      window.location.href = window.location.href.split('?')[0];
      return;
    }

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="fixed" className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={() => this.toggleDrawer(true)}
              className={classes.menuButton}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit" noWrap className={classes.grow}>
              {title}
            </Typography>
            {
              !!viewingHome ? undefined : (
              <IconButton // TODO Only if an exercise is open
                color="inherit"
                aria-label="Close Exercise"
                onClick={() => this.viewHome()}
              >
                <CloseIcon />
              </IconButton>)
            }
            
          </Toolbar>
        </AppBar>
        <nav className={classes.drawer}>
          {<Drawer params={params} isAdmin={isAdmin} drawerOpen={drawerOpen} navigate={this.navigate.bind(this)} viewHome={this.viewHome.bind(this)} />}
        </nav>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          {content}
        </main>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, mapDispatchToProps)(App));
