import React from 'react';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';

import { Divider, Drawer, Hidden, List, ListSubheader, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';

import LockOpenIcon from '@material-ui/icons/LockOpen';
import KeyIcon from '@material-ui/icons/VpnKey';
import HomeIcon from '@material-ui/icons/Home';

import ViewExercisePage from '../pages/ViewExercise';
import PasswordPage from '../pages/Password';

import { getExercises, getExtraExercises } from '../assets/exercises';

export const drawerWidth = 240;

const styles = theme => ({
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  }
});

class Exercise extends React.Component {
  render() {
    const { title, onClick } = this.props;
    return (
      <ListItem button onClick={onClick}>
        <ListItemIcon><LockOpenIcon /></ListItemIcon>
        <ListItemText primary={title} />
      </ListItem>
    );
  }
}

class AppDrawer extends React.Component {
  viewExercise({title, exercise, description}) {
    this.props.navigate({
      title,
      content: <ViewExercisePage description={description} exercise={exercise} />,
    })
  }

  enterPassword() {
    this.props.navigate({
      title: 'Enter Password',
      content: <PasswordPage mode={this.props.params.mode} />
    });
  }

  render() {
    const { classes, theme, params: { mode } } = this.props;
    const { exercises: { [mode]: { latestPassword } = {}} = {} } = this.props;
    const exercises = getExercises(mode, latestPassword);
    const extraExercises = getExtraExercises(mode);
    const drawer = (
      <div>
        <div className={classes.toolbar}>
          <List>
            <ListItem button onClick={() => this.props.viewHome()}>
              <ListItemIcon><HomeIcon /></ListItemIcon>
              <ListItemText primary="Home" />
            </ListItem>            
          </List>
        </div>
        <Divider />
        <List subheader={<ListSubheader component="div">Exercises</ListSubheader>}>
          {exercises.map(({title, exercise}, index) => (
            <Exercise title={title} key={`${title}-${index}`} onClick={() => this.viewExercise({title, exercise})}/>
          ))}
        </List>
        <Divider />
        {
          !extraExercises || extraExercises.length === 0 ? 
          undefined
          : (
          <>
            <List subheader={<ListSubheader component="div">Extra Exercises</ListSubheader>}>
              {extraExercises.map(({title, description, exercise}, index) => (
                <Exercise title={title} key={`${title}-${index}`} onClick={() => this.viewExercise({title, exercise, description})}/>
              ))}
            </List>
            <Divider />
          </>
          )
        }
        
        <List>
          <ListItem button onClick={() => this.enterPassword()}>
            <ListItemIcon><KeyIcon /></ListItemIcon>
            <ListItemText primary="Enter Password" />
          </ListItem>          
        </List>
      </div>
    );
    return (
      <>
        <Hidden smUp implementation="css">
        <Drawer
              container={this.props.container}
              variant="temporary"
              anchor={theme.direction === 'rtl' ? 'right' : 'left'}
              open={this.props.drawerOpen}
              onClose={() => this.toggleDrawer(false)}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </>
    )
  }
}

const mapStateToProps = state => ({
  ...state
});

const mapDispatchToProps = dispatch => ({
});

export default withStyles(styles, { withTheme: true })(connect(mapStateToProps, mapDispatchToProps)(AppDrawer));
