import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { ExpansionPanel, ExpansionPanelDetails, ExpansionPanelSummary, Typography } from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

const styles = theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },

  card: {
    minWidth: 275,
    marginTop: '16px'
  },
  pos: {
    marginBottom: 12,
  },
});

class _Mission extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      expanded: false
    }
  }
  toggleExpanded() {
    this.setState({
      expanded: !this.state.expanded
    });
  }
  indexToMissionName(index) {
    const letter = String.fromCharCode(97 + index).toUpperCase();
    return <strong>Mission {letter}</strong>;
  }
  render() {
    const { description, points, exercise, index, classes } = this.props;
    const { expanded } = this.state;
    return (
        <ExpansionPanel expanded={expanded} onChange={() => this.toggleExpanded()}>
          <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
            <Typography className={classes.heading}>{this.indexToMissionName(index)} ({points} Nectar Points)</Typography>
            <Typography className={classes.secondaryHeading}>{description}</Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails>
            <div>
            {exercise}
            </div>
          </ExpansionPanelDetails>
        </ExpansionPanel>
    );
  }
}
const Mission = withStyles(styles)(_Mission);

class _Missions extends React.Component {
  render() {
    const { missions, classes } = this.props;
    return (
      <div className={classes.root}>
        {missions.map(({description, points, exercise}, index) => <Mission key={description} index={index} description={description} points={points} exercise={exercise} />)}
      </div>
    )
  }
}

const Missions = withStyles(styles)(_Missions);

class _AssistanceItem extends React.Component {
  render() {
      const { title, description, points, classes } = this.props;
      return (
        <Card className={classes.card}>
          <CardContent>
            <Typography variant="h5" component="h2">
              {title}
            </Typography>
            <Typography variant="subtitle1" component="h2">
              {description}
            </Typography>
            <Typography className={classes.pos} color="textSecondary">
              {points} Points
            </Typography>
          </CardContent>
        </Card>
      );      
  }
}

const AssistanceItem = withStyles(styles)(_AssistanceItem);

class _Assistance extends React.Component {
  render() {
    const { assistance, classes } = this.props;
    if (!assistance) {
      return undefined;
    }
    const { title, items = [] } = assistance;
    return (<div className={classes.root}>
      <Typography variant="h5" component="h2">{title}</Typography>
      {items.map(({title, description, points}, index) => <AssistanceItem key={index} title={title} description={description} points={points}/>)}
    </div>)
  }
}

const Assistance = withStyles(styles)(_Assistance);


class ViewExercise extends React.Component {
  render() {
    const { description, exercise: { missions, assistance = [] } } = this.props;
    return (
      <>
      {
        !description ? 
        undefined 
        : <>
        <Typography variant="h5" component="h2">{description}</Typography>
        <div style={{marginTop: '32px'}} />
        </>
      }
      <Missions missions={missions} />
      <div style={{marginTop: '32px'}} />
      {assistance.map((a, index) => <Assistance assistance={a} key={index} />)}
      </>
    );
    
  }
}

export default ViewExercise;